import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPengalamanService } from 'src/app/sdk/hr/services';
import { Pelamar_pengalaman } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPelamarPengalamanService: DirektoriPelamarPengalamanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService
    ) { }

    id_pelamar: string = ''
    dataPengalaman: Pelamar_pengalaman[] = []

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.getPengalaman()
    }


    getPengalaman() {
        this.direktoriPelamarPengalamanService.getDirektoriPelamarPengalamen(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataPengalaman = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPelamarPengalamanService
            .deleteDirektoriPelamarPengalamenId(id)
            .subscribe(data => {
                this.getPengalaman()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'pengalaman','list'])
    }



}
