import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPengalamanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_pengalaman } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPelamarPengalamanService: DirektoriPelamarPengalamanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pelamar: string = ''
    dataPengalaman: Pelamar_pengalaman = {}
    id_pelamar_pengalaman: string = ''

    ngOnInit() {
        this.id_pelamar_pengalaman = this.activatedRoute.snapshot.paramMap.get('id_pelamar_pengalaman')
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.form()
        this.getData()
    }

    getData() {
        this.direktoriPelamarPengalamanService.getDirektoriPelamarPengalamenId(this.id_pelamar_pengalaman).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar_pengalaman>
    form() {
        this.dataForm = this.fb.group<Pelamar_pengalaman>({
            jabatan_terakhir: new FormControl('', [Validators.required]),
            nama_perusahaan: new FormControl(''),
            tanggal_masuk: new FormControl(''),
            tanggal_keluar: new FormControl(''),
            tugas_utama: new FormControl(''),
            alasan_keluar: new FormControl(''),
            gaji_terakhir: new FormControl(''),
            status: new FormControl(''),
            nama_referensi: new FormControl(''),
            jabatan_referensi: new FormControl(''),
            contact_referensi: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            mata_uang: new FormControl(''),
            id_pelamar_pengalaman: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            no_dokumen: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPengalaman = this.dataForm.value
        this.direktoriPelamarPengalamanService
            .putDirektoriPelamarPengalamenId({id: this.id_pelamar_pengalaman, body: this.dataPengalaman})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'pengalaman', 'list'])
    }


}
