import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PengalamanRoutingModule } from './pengalaman-routing.module';
import { EditComponent } from './edit/edit.component';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DetailComponent } from './detail/detail.component';


@NgModule({
    declarations: [EditComponent, ListComponent, AddComponent, DetailComponent],
    imports: [
        CommonModule,
        PengalamanRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PerfectScrollbarModule,
    ],
    providers:
        [
            FormBuilderTypeSafe
        ],
})
export class PengalamanModule { }
