import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarPengalamanService, DirektoriPegawaiPengalamanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_pengalaman, Pegawai_pengalaman } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPegawaiPengalamanService: DirektoriPegawaiPengalamanService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pegawai: string = ''
    dataPengalaman: Pegawai_pengalaman = {}
    id_pegawai_pengalaman: string = ''

    ngOnInit() {
        this.id_pegawai_pengalaman = this.activatedRoute.snapshot.paramMap.get('id')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })
        this.form()
        this.getData()
    }

    getData() {
        this.spinner.show()
        this.direktoriPegawaiPengalamanService.getDirektoriPegawaiPengalamenId(this.id_pegawai_pengalaman).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai_pengalaman>
    form() {
        this.dataForm = this.fb.group<Pegawai_pengalaman>({
            jabatan_terakhir: new FormControl('', [Validators.required]),
            nama_perusahaan: new FormControl(''),
            tanggal_masuk: new FormControl(''),
            tanggal_keluar: new FormControl(''),
            tugas_utama: new FormControl(''),
            alasan_keluar: new FormControl(''),
            gaji_terakhir: new FormControl(''),
            status: new FormControl(''),
            id_dokumen: new FormControl(''),
            nama_referansi: new FormControl(''),
            kontak_referensi: new FormControl(''),
            jabatan_referensi: new FormControl(''),
            mata_uang: new FormControl(''),
            id_user: new FormControl(''),
            id_pegawai_pengalaman: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            no_dokumen: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPengalaman = this.dataForm.value
        this.direktoriPegawaiPengalamanService
            .putDirektoriPegawaiPengalamenId({id: this.id_pegawai_pengalaman, body: this.dataPengalaman})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/employee/pegawai-detail', this.id_pegawai,'biodata', 'pengalaman', 'list'])
    }


}
